@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;1,100;1,200;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;1,100;1,200;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;1,100;1,200;1,300&display=swap);
.navigation-bar {
  width: 100%;
  position: fixed;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  height: 80px;
  display: flex;
  align-items: center;
  padding-left: 20px;
  background-image: linear-gradient(
    to bottom,
    rgb(149, 174, 243),
    rgb(238, 234, 234)
  );
  border-radius: 0 0 5px 5px;
  box-shadow: 0px 2px 6px rgb(151, 151, 151);
  z-index: 100;
}
.navigation-bar h1 {
  color: black;
}

.logo {
  background-image: url(/triplan-react-redux-firebase/static/media/logo2.11824906.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 160px;
  height: 70px;
  /* border: 1px solid red; */
}
.logo:hover {
  background-image: url(/triplan-react-redux-firebase/static/media/logoChange2.8fa3a984.png);
}

.error__container {
  position: absolute;
  background: rgba(211, 196, 196, 0.92);

  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 75px;
  box-shadow: inset 0 0 15px black;

  border-radius: 3px;
  line-height: 150px;
  -webkit-text-stroke: 2px black;

  color: rgb(189, 187, 187);
  -webkit-animation: fade 4s;
          animation: fade 4s;
}

@-webkit-keyframes fade {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fade {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.new-trip__container {
  z-index: 100;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.52),
    rgb(93, 93, 93)
  );
  display: flex;
  justify-content: center;
  align-items: center;
}
.close-from-outside {
  background-color: rgba(111, 0, 255, 0);
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  position: absolute;
}
.new-trip__form-container {
  width: 60%;
  height: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(
    to bottom,
    rgba(241, 241, 241, 0.87),
    rgb(207, 207, 207)
  );
  border-radius: 5px;
  position: relative;
}
.new-trip__form-container h1 {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
}
.new-trip__form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 60%;
  height: 80%;

  border-radius: 5px;
}
.new-trip__form section {
  display: flex;
  align-items: center;
  justify-content: center;
}
.close {
  position: absolute;
  top: 20px;
  right: 30px;
  font-size: 60px;
}
.input_note {
  color: red;
}

.trips-list__container {
  margin-top: 15px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  width: 60%;
  grid-gap: 20px;
  gap: 20px;
}
.trip__container {
  position: relative;
}
.trip_details {
  margin-top: 5px;
  border-radius: 6px;
  background-image: linear-gradient(
    200deg,
    rgba(255, 255, 255, 0.479),
    rgba(2, 195, 243, 0.226),
    rgba(255, 255, 255, 0.664)
  );
  /* border: 1px solid rgb(121, 121, 121); */
  box-shadow: inset 0px 0px 5px rgb(99, 99, 99);

  width: 100%;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
}
.trip_details:hover {
  background-image: linear-gradient(
    200deg,
    rgba(255, 255, 255, 0.719),
    rgba(2, 195, 243, 0.582),
    rgba(255, 255, 255, 0.795)
  );
  box-shadow: inset 0px 0px 10px rgb(0, 0, 0);
}
.trip_details span {
  color: black;
  font-size: 35px;
  font-weight: 800;
}

.trip_delete-button {
  position: absolute;
  border-radius: 2px;
  padding: 10px;
  background-image: linear-gradient(
    45deg,
    rgba(245, 85, 85, 0.795),
    rgba(255, 49, 49, 0.301)
  );
  border: none;
  cursor: pointer;
  width: 60%;
  margin-top: -50px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.trip_delete-button:hover {
  box-shadow: inset 0px 0px 3px rgb(0, 0, 0);
  /* border: 1px solid black; */
  background-image: linear-gradient(
    to bottom,
    rgba(252, 89, 89, 0.685),
    rgba(255, 82, 82, 0.747)
  );
}

.already-exist__container {
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  position: absolute;
  background-color: rgba(22, 20, 26, 0.452);

  display: flex;
  justify-content: center;
  align-items: center;
}
.already-exist {
  width: 60%;
  height: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(
    to bottom,
    rgba(241, 241, 241, 0.87),
    rgb(207, 207, 207)
  );
  border-radius: 5px;
  position: relative;
  align-items: center;
}
.already-exist h1 {
  font-family: "Poppins", sans-serif;

  text-align: center;
  color: rgb(20, 46, 46);
  font-size: 56px;
}

.all-trips__container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  flex-direction: column;
  font-family: "Poppins", sans-serif;
}
.all-trips__header {
  font-size: 46px;
  font-family: "Poppins", sans-serif;
}
.add-trip__icon {
  box-shadow: -2px 0px 3px black;
  padding: 25px;
  border: 1px solid rgb(92, 92, 92);
  border-radius: 20%;
  font-size: 24px;
  background-image: linear-gradient(
    200deg,
    rgba(255, 255, 255, 0),
    rgba(179, 205, 252, 0.925),
    rgba(255, 255, 255, 0)
  );
  color: rgb(65, 65, 65);
  cursor: pointer;
}
.add-trip__icon:hover {
  box-shadow: inset 0px 0px 4px black;

  background-image: linear-gradient(
    200deg,
    rgba(179, 205, 252, 0.925),
    rgba(255, 255, 255, 0),
    rgba(179, 205, 252, 0.925)
  );
}

i.plus.icon.cancel::before {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.add-trip__icon i {
  margin: 0;
}
.add-trip__icon h2 {
  font-family: "Poppins", sans-serif;
}
.all-trips_message {
  margin-top: 200px;
  width: 60%;
  font-size: 60px;
  text-shadow: 0px 0px 3px rgb(13, 13, 14);
  color: rgb(26, 89, 170);
  background-color: rgba(255, 0, 0, 0);
  -webkit-animation: show-message 2s;
          animation: show-message 2s;
}

@-webkit-keyframes show-message {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes show-message {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.new-plan__container {
  /* transition: ease-in-out 1s; */

  position: absolute;
  right: 350px;
  top: 150px;
  background-image: linear-gradient(
    to top,
    rgb(200, 240, 250),
    rgb(187, 225, 236)
  );
  box-shadow: 0px 0px 10px black;
  border-radius: 15px;
  padding: 20px 80px;
  -webkit-animation: show 200ms;
          animation: show 200ms;
}

@-webkit-keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
i.icon.window.close:before {
  font-size: 25px;
}
.textarea-container {
  resize: none;
  padding: 5px;
  background-color: #e6faf7d7;
}
.error_message {
  position: absolute;
  color: red;
}

.plan__container {
  font-family: "Poppins", sans-serif;

  border: 1px solid rgba(0, 110, 255, 0.479);
  width: 100%;
  display: flex;
  align-items: center;
  margin-top: 10px;
  padding: 10px;
  box-shadow: 0px 0px 2px black;
  border-radius: 5px;
  background-image: linear-gradient(
    to bottom,
    rgba(175, 205, 206, 0.438),
    rgba(196, 194, 194, 0.199)
  );
  -webkit-animation: show 0.5s;
          animation: show 0.5s;
}

@-webkit-keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.plan__container section {
  font-family: "Poppins", sans-serif;
  width: 93%;
  display: flex;
  align-items: center;
}
.plan__day {
  border-right: 1px solid black;
  padding-right: 10px;
}
.plan__plan-details {
  padding-left: 10px;
  overflow: auto;
}
h1 {
  font-family: "Poppins", sans-serif;

  margin: 0px;
}

h2 {
  padding: 2px;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  margin: 0px;
}
.plan__delete-button {
  border-radius: 3px;
  padding: 10px;
  margin-left: 5px;
  width: 7%;
  background-image: linear-gradient(
    45deg,
    rgba(245, 85, 85, 0.795),
    rgba(255, 49, 49, 0.301)
  );
  height: 100%;
  border: none;
  cursor: pointer;
  font-family: "Poppins", sans-serif;
}
.plan__delete-button:hover {
  box-shadow: inset 0px 0px 3px rgb(0, 0, 0);
  /* border: 1px solid black; */
  background-image: linear-gradient(
    to bottom,
    rgba(252, 89, 89, 0.685),
    rgba(255, 82, 82, 0.747)
  );
}

.trip-details__container {
  /* border: 1px solid red; */
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
}
.trip-details__header {
  display: flex;
  width: 100%;
  margin-top: 3px;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(
    to bottom,
    rgba(104, 151, 163, 0.692),
    rgba(255, 255, 255, 0)
  );
  border-radius: 5px 5px 15px 15px;
  -webkit-animation: toRight 4s;
          animation: toRight 4s;
}

@-webkit-keyframes toRight {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes toRight {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
.trip-details__country {
  font-size: 56px;
  padding: 15px 30px;

  margin-top: 30px;
}

.cancel {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);

  /* transition: linear 1s; */
}

.page-container {
  padding-top: 80px;
}

